$main_color : #ff0000;

/* mixin - 제일 위에 선언 */
@mixin func() {
  background : #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width : 100%;
  margin : auto;
}

.red {
  color : $main_color;
}

/* nesting */
/*div.container {
  h4 {
    color : blue;
  }
  p {
    color : green;
  }
}*/

.my-alert {
  background : #eeeeee;
  padding : 15px;
  border-radius : 5px;
  max-width : 500px;
  width : 100%;
  margin : auto;
  font-weight: bold;
}

/* extend */
.my-alert2 {
  @extend .my-alert;
  background : yellow;
}

.my-alert3 {
  @extend .my-alert;
  background: aliceblue;
}

.saleP {
  margin-bottom: 10px !important;
}

/* 함수 사용
.my-alert {
  @include func()
}
 */

.my-alert p {
  margin-bottom : 0;
}

.mar {
  margin : 10px;
}

.wow-enter {
  opacity : 0;
  color : red;
}

.wow-enter-active {
  opacity : 10;
  transition : all 500ms;

}